const COLORS = "background: #000920; color: #fff";

export default message => {
  switch (message.type) {
    case "init":
      console.clear();
      console.log("%c ☁️ initialized.", COLORS);
      console.log(
        `%c ☁️ Browser: ${message.payload.identity.userAgent}`,
        COLORS
      );
      console.log(`%c ☁️ IP: ${message.payload.identity.sourceIp}`, COLORS);
      break;

    default:
      //@todo: check if we have a valid type here.
      console[message.type](message.payload);
  }
};
