export function getClientsInitLogs(data) {
  return data.reduce((accu, item) => {
    if (accu.map(i => i.client).indexOf(item.client) > -1) {
      return accu;
    }

    if (item.type === "init") {
      accu.push(item);
    }
    return accu;
  }, []);
}

/**
 * @deprecated since ever.
 */
export function getSessionsForClient(data, client) {
  return data.reduce((accu, item) => {
    if (accu.map(i => i.id).indexOf(item.session) > -1) {
      return accu;
    }
    if (item.client === client) {
      accu.push({
        id: item.session,
        name: `session-${item.session}`,
        createdAt: item.createdAt
      });
    }
    return accu;
  }, []);
}

/**
 * @deprecated since ever.
 */
export function getRequestsForSession(data, session) {
  return data.reduce((accu, item) => {
    if (accu.map(i => i.id).indexOf(item.request) > -1) {
      return accu;
    }
    if (item.session === session) {
      accu.push({
        id: item.request,
        name: `request-${item.request}`,
        createdAt: item.createdAt
      });
    }
    return accu;
  }, []);
}

export function getInitLogsForClient(data, client) {
  return data.reduce((accu, item) => {
    if (item.client === client && item.type === "init") {
      accu.push(item);
    }
    return accu;
  }, []);
}
