import { h, render, Component } from "preact";
import { useState, useCallback, useEffect } from "preact/hooks";
import {
  getClientsInitLogs,
  getSessionsForClient,
  getRequestsForSession,
  getInitLogsForClient
} from "../util/streamfilter";
import moment from "moment";

function naming(log) {
  return `${moment(log.createdAt)} / Session: ${log.session} / Request: ${
    log.request
  }`;
}

export default props => {
  const { entities, client } = props;

  const inits = getInitLogsForClient(entities, client);

  return (
    <ul>
      {inits.map(log => {
        const hash = `#${log.request}`;
        return (
          <li>
            <a
              href={hash}
              onClick={() => {
                window.history.pushState({}, naming(log), hash);
                window.dispatchEvent(new HashChangeEvent("hashchange"));
              }}
            >
              {naming(log)}
            </a>
          </li>
        );
      })}
    </ul>
  );
};
