import "regenerator-runtime/runtime";

import { h, render, Component } from "preact";
import { useState, useCallback, useEffect } from "preact/hooks";
import { Button } from "preact-material-components";

import "preact-material-components/style.css";
import moment from "moment";
import awsIot from "aws-iot-device-sdk";
import parser from "./util/parser";
import Selector from "./components/Clients";
import Clients from "./components/Clients";

let AWSIOTCLIENT;
let TOPIC;
function App() {
  const [user, setUser] = useState(undefined);

  useEffect(() => {
    const searchParams = new URLSearchParams(window.location.search);
    const code = searchParams.get("code");
    const githubAccessToken = localStorage.getItem("githubAccessToken");
    if (!code && !githubAccessToken) return false;

    fetch(`${process.env.CLOUDLOG_API_URL}/ui-auth`, {
      method: "GET", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, *cors, same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: {
        Authorization: githubAccessToken
          ? `token ${githubAccessToken}`
          : `code ${code}`
      },
      redirect: "follow", // manual, *follow, error
      referrerPolicy: "no-referrer" // no-referrer, *client
    })
      .then(response => {
        if (!response.ok) {
          localStorage.removeItem("githubAccessToken");
          console.error(
            "Github Login failed. Try again or contact support@cloudlog.dev"
          );
        }
        return response.json();
      })
      .then(response => {
        // console.dir(response);
        localStorage.setItem("githubAccessToken", response.githubAccessToken);
        setUser(response);

        TOPIC = response.topic;
        // console.log(TOPIC);
        window.history.replaceState({}, document.title, "/");
      });

    return () => {};
  }, []);

  const [entities, setEntities] = useState(require("../tests/data.json"));
  useEffect(() => {
    if (!user) {
      return false;
    }

    if (!AWSIOTCLIENT) {
      AWSIOTCLIENT = awsIot.device({
        region: user.region,
        protocol: "wss",
        accessKeyId: user.accessKey,
        secretKey: user.secretKey,
        sessionToken: user.sessionToken,
        port: 443,
        host: user.iotEndpoint
      });
    }
    AWSIOTCLIENT.on("connect", () => {
      AWSIOTCLIENT.subscribe(TOPIC); // subscribe to a topic

      // console.log("Connected UI");
    });

    AWSIOTCLIENT.on("close", () => {
      // console.log("Connection failed");
    });

    AWSIOTCLIENT.on("message", (topic, message) => {
      message = JSON.parse(message.toString());
      if (message) {
        setEntities(entities => [
          ...entities,
          { ...message, createdAt: moment().toISOString() }
        ]);
      }
      parser(message);
    });

    return () => {
      AWSIOTCLIENT.end();
    };
  }, [user, entities]);

  const [hash, setHash] = useState(null);
  const hashListener = event => {
    console.log(event);

    setHash(window.location.hash.substring(1));
  };

  useEffect(() => {
    window.addEventListener("hashchange", hashListener, false);

    return () => {
      window.removeEventListener("hashchange", hashListener, false);
    };
  }, [hash]);

  const login = () => {
    window.location = `https://github.com/login/oauth/authorize?scope=user:email&client_id=${process.env.GITHUB_CLIENT_ID}`;
  };

  console.dir(entities);
  return (
    <div>
      <Button onClick={login}>Login</Button>to begin!
      {/* <Button onClick={incrementCb}>incrementCb</Button> */}
      <Clients entities={entities} />
      <p>Hash: {hash}</p>
    </div>
  );
}
render(<App />, document.body);
