import { h, render, Component } from "preact";
import { useState, useCallback, useEffect } from "preact/hooks";
import { getClientsInitLogs } from "../util/streamfilter";
import Inits from "./Inits";
export default props => {
  const entities = props.entities;

  const logs = getClientsInitLogs(entities);

  return (
    <main>
      {logs.map(log => {
        return (
          <section>
            <h3>
              {log.payload.identity.sourceIp} / {log.payload.identity.userAgent}{" "}
              / {log.client.id}
            </h3>
            <Inits entities={entities} client={log.client} />
          </section>
        );
      })}
    </main>
  );
};
